/* eslint-disable no-param-reassign */

const jourSemaine = ['Activité ', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche'];


// ajouter une activité

const tableEl = document.createElement('table');
const trEl = document.createElement('tr');
tableEl.appendChild(trEl);
// eslint-disable-next-line for-direction
for (const jour of jourSemaine) {
  const tdEl = document.createElement('td');
  trEl.appendChild(tdEl);
  tdEl.innerText = jour;
  tdEl.style.color = ' blue';
}
document.body.appendChild(tableEl);
// les activites realise
const btn = document.getElementById('addBtn');
let itemCounter = 1;
const input = document.getElementById('myInput');
btn.addEventListener('click', () => {
  const trActivite = document.createElement('tr');
  const tdActivite = document.createElement('td');

  // create the close btn
  const close = document.createElement('span');
  close.innerText = '\u00D7   ';
  close.setAttribute('id', `close${itemCounter}`);


  tdActivite.appendChild(close);
  tdActivite.innerHTML += input.value;
  tdActivite.style.color = 'green';
  trActivite.appendChild(tdActivite);

  for (let i = 0; i < 7; i++) {
    const td = document.createElement('td');
    trActivite.appendChild(td);
    td.addEventListener('click', (event) => {
      if (event.target.innerHTML !== ' ') {
        // eslint-disable-next-line no-param-reassign
        event.target.innerHTML = ' ';
      } else {
        event.target.innerHTML = '  &bull;';
      }
      event.target.style.fontSize = '40px';
    });
  }
  // eslint-disable-next-line no-restricted-globals
  // eslint-disable-next-line no-template-curly-in-string


  tableEl.appendChild(trActivite);
  document.getElementById(`close${itemCounter}`).addEventListener('click', () => {
    tableEl.removeChild(trActivite);
  });
  itemCounter++;
  input.value = ' ';
});
